export const movies = state => state.moviesStore.movies;
export const movie = state => state.moviesStore.currentMovie;

export const planets = state => state.planetsStore.planets;
export const planet = state => state.planetsStore.currentPlanet;
export const loadingPlanets = state => state.planetsStore.loadingPlanets;



